@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
   padding: 0;
   margin: 0;
   box-sizing: border-box;
   font-family: 'Mulish', sans-serif;
}

body {
   height: 100vh;
   width: 100vw;
   overflow-x: hidden;
   padding-top: 4rem;
}

.navrow {
   background: #fff !important;
   position: fixed;
   width: 100vw !important;
   top: 0px;
   right: 12px;
   box-shadow: 0px 3px 7px rgba(0,0,0,0.2) !important;
   z-index: 99;
}

.navbar {
   background-color: #fff !important;
}

.navbar-brand {
   font-size: 1.9rem !important;
   color: #565387 !important;
}

.navbar a {
   font-size: 1.3rem;
   text-transform: capitalize;
}

.menu_active {
   font-weight: bold;
   border-bottom: 1px solid #565387;
}

.navbar a:hover {
   color: #3498db !important;
}

@media (max-width: 991px) {
   .navbar-brand {
      font-size: 1.6rem !important;
   }

   .menu_active {
      font-weight: bold;
      border-bottom: none;
   }
}


.homeSection {
   position: relative;
   height: calc(100vh - 66px);
   overflow: hidden;
   background-image: url(/static/media/backgroundImg.25a237b5.png);
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
}

#header {
   width: 100%;
}

#header h1 {
   margin: 0 0 10px 0;
   font-size: 48px;
   font-weight: 700;
   color: black;
}

#header .brand-name {
   color: #3498db;
}

#header h2 {
   color: #484848;
   font-size: 24px;
}

#header .btn-get-started {
   font-weight: 500;
   font-size: 16px;
   letter-spacing: 1px;
   display: inline-block;
   padding: 10px 30px;
   border-radius: 50px;
   transition: 0.3s;
   color: #3498db;
   border: 3px solid #3498db;
   text-decoration: none;
   font-weight: bold;
   -webkit-backdrop-filter: blur(30px);
           backdrop-filter: blur(30px);
}

#header .btn-get-started:hover {
   background: #3498db;
   color: #fff;
}

.header-img {
   text-align: right;
}

#header .animated {
   animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
   0%{
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
   }
   100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
   }
}

@keyframes up-down {
   0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
   }
   100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
   }
}


/* ============ card ========== */

.card {
   border-radius: 2rem;
   overflow: hidden;
   box-shadow: 0px 2px 10px rgba(0,0,0,0.19);
   cursor: pointer;
   transition: all 1s ease;
}
.card:hover {
   -webkit-transform: scale(0.95);
           transform: scale(0.95);
}


/* =========== service ========= */

.serviceBox {
   margin-top: 4px;
   height: calc(100vh - 70px);
   overflow-y: scroll;
}

/* =========== contact box ========= */

.contactBox {
   width: 100vw;
   height: 90vh;
   padding-bottom: 2rem;
}

.contactBox,
.contactBox .contactImgBox {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items: center;
           align-items: center;
   overflow-x: hidden;
}

.contactBox .row {
   width: 100% !important;
   height: -webkit-max-content;
   height: max-content;
}

.contactImgBox img {
   margin-top: 4rem;
   width: 80%;
   animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.contactFormBox form {
   border-radius: 2rem;
   box-shadow: 3px 5px 12px rgba(0,0,0,0.2);
   margin-top: 1rem;
}
